import service from '@/utils/service'

// api 
/**
 * @desc get
 * @param  {Object} params 
 * @return {Object|Promise} Promise
 */
export function getReqTest(params) {
  return service.request({
    url: `/facts`,
    method: 'GET',
    params,
  })
}
/**
 * @desc post
 * @param  {Object} data 
 * @return {Object|Promise} Promise
 */
export function postReqTest(data) {
  return service.request({
    url: `/api/postTest`,
    method: 'POST',
    data,
  })
}

