<template>
  <div class="v-map">
    <div id="map" :style="{ width: width + 'px', height: height + 'px' }"></div>
    <div class="court-dialog" v-show="courtLength >= 1">
      <div class="court-pointer">
        <p class="circle"></p>
        <p class="dashed"></p>
        <p class="circle"></p>
      </div>
      <div class="court-wrapper">
        <div class="court">
          <img :src="courtInfo1.picList && courtInfo1.picList[0]" alt="" />
          <h3>{{ courtInfo1.name }}</h3>
          <h4>{{ courtInfo1.country }}</h4>
        </div>
        <div class="duration">
          <span></span>
          <strong class="time" v-show="courtLength === 2">{{ flDur }}</strong>
        </div>
        <div v-show="courtLength === 1"></div>
        <div class="court" v-show="courtLength === 2">
          <h3>{{ courtInfo2.name }}</h3>
          <h4>{{ courtInfo2.country }}</h4>
          <img :src="courtInfo2.picList && courtInfo2.picList[0]" alt="" />
        </div>
      </div>
      <div class="court-close" @click="courtCloseClick"><span class="iconfont icon-guanbi"></span></div>
    </div>
    <div class="map__tip">
      <div class="content">()~~</div>
    </div>
  </div>
</template>

<script>
