          value: [19.095278, 47.475556, '', 'psks'],
          sdi: 0,
        },
        {
          cid: 21,
          name: '',
          value: [26.622, 44.2556, '', 'bjls'],
          sdi: 0,
        },
        {
          cid: 22,
          name: '',
          value: [49.91979, 40.429766, '', 'bkalpk'],
          sdi: 0,
        },
        {
          cid: 23,
