          value: [30.1242, 59.582, '', 'klstfsj'],
          sdi: 0,
        },
        {
          cid: 24,
          name: '',
          value: [12.57222, 55.7025, '', 'pk'],
          sdi: 0,
        },
      ]
      options = {
        tooltip: {
          formatter: params => {
            let ele = ''
            let cid = params.data.cid
            let name = params.name
            const courtPicUrl = courtsList.find(court => court.cid === cid).picList[0]
            // var a = '<br> <a id="map_tip" style="color: red"></a>'
            // setTimeout(() => {
            //   document.getElementById('map_tip').onclick = function() {
            //     console.log(params.value)
            //   }
            // }, 500)
            // return params.name + ': ' + value[2] + a
            ele = `
                <div style="width:180px;">
                  <img style="width:100%;" src='${courtPicUrl}' />
                  <h3 style="text-align:center;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;">${name}</h3>
                </div>
              `
            return ele
          },
          enterable: true,
          hideDelay: 100, // ,
        },
        geo: {
          type: 'map',
          map: 'worldMap',
          nameMap: {
            Afghanistan: '',
            Albania: '',
            Algeria: '',
            Angola: '',
            Argentina: '',
            Armenia: '',
            Australia: '',
            Austria: '',
            Azerbaijan: '',
