            Mauritania: '',
            Mauritius: '',
            Mexico: '',
            Moldova: '',
            Monaco: '',
            Mongolia: '',
            Montenegro: '',
            Morocco: '',
            Mozambique: '',
            Myanmar: '',
            Namibia: '',
            Nepal: '',
            Netherlands: '',
            'New Caledonia': '',
            'New Zealand': '',
            Nicaragua: '',
            Niger: '',
            Nigeria: '',
            Norway: '',
            Oman: '',
            Pakistan: '',
            Panama: '',
            'Papua New Guinea': '',
            Paraguay: '',
            Peru: '',
            Philippines: '',
            Poland: '',
            Portugal: '',
            'Puerto Rico': '',
            Qatar: '',
            Reunion: '',
