          },
          label: {
            show: true,
            color: '#fff',
          },
          zoom: 6,
          roam: true,
          center: [13.8, 54.0],
          silent: true,
          itemStyle: {
            color: '#131b2e', // 
            borderColor: '#ccc',
            // emphasis: {
            // color: 'rgba(19,27,46,0)',
            // borderColor: '#ccc',
            // },
          },
        },
        series: [
          {
            type: 'effectScatter',
            data: scatterData,
            coordinateSystem: 'geo',
            rippleEffect: {
              scale: 3,
            },
            symbolSize: 15,
            showEffectOn: 'emphasis',
            itemStyle: {
              color: function(ev) {
                // console.log('color-ev', ev)
                if (ev.data.sdi) {
                  return '#ff5e57' // active
                }
                // return '#f1f2f6' // not active
                return '#3fbdd0' // not active
              },
            },
          },
          {
            type: 'lines',
            zlevel: 1,
            effect: {
              show: true,
              // symbol: 'diamond',
            },
            silent: true,
            lineStyle: {
              curveness: 0.3,
              width: 1,
              type: 'solid',
              color: '#ff5e57',
            },
            animationDuration: 1500,
            data: [],
          },
        ],
      }
      let chartDom = document.getElementById('map')
      mapChart = echarts.init(chartDom)
      const res = await axios.get('/lib/world.json')
      echarts.registerMap('worldMap', res.data)
      mapChart.on('click', this.handleMapClick)
      mapChart.setOption(options)
    },
    handleMapClick(params) {
      // console.log('city:', params)

      let longitude = params.value[0]
      let latitude = params.value[1]
      let courtIndex = params.dataIndex
      let len = courtControl.length
      let cid = params.data.cid

      scatterData[courtIndex].sdi = 1 // 
      let selectIndex = courtControl.indexOf(courtIndex)
      let tempCourt = courtsList.filter(item => item.cid === cid)[0]
      switch (len) {
        case 0:
          courtControl.push(courtIndex) // 
          lngAndLatStart['coord'] = [longitude, latitude] // start

          // fisrt court show
          this.courtLength = 1
          // court
          this.courtInfo1 = tempCourt

          this.cid1 = cid
          break
        case 1:
          if (selectIndex !== -1) {
            /* courtcourt */
            scatterData[courtIndex].sdi = 0 // 
            courtControl.splice(selectIndex, 1)
            lngAndLatStart['coord'] = [] // start

            // fisrt court hidden
            this.courtLength = 0
            // court
            this.courtInfo1 = {}

            this.cid1 = 0
          } else {
            /* court */
            courtControl.push(courtIndex) // 
            lngAndLatEnd['coord'] = [longitude, latitude] // End
            options.series[1].data = [[lngAndLatStart, lngAndLatEnd]]

            // second court show
            this.courtLength = 2
            // court
            this.courtInfo2 = tempCourt
            this.cid2 = cid
            const tpFlDur = flightDuration.find(item => item.Venue1 === this.cid1 && item.Venue2 === this.cid2)
            this.flDur = tpFlDur.Duration
          }
          break
        case 2:
          if (selectIndex === 0) {
            /* courtcourt*/
            scatterData[courtIndex].sdi = 0 // 
            courtControl.splice(selectIndex, 1)
            lngAndLatStart['coord'] = lngAndLatEnd['coord']
            options.series[1].data = []

            this.courtLength = 1
            // courtcourtcourt
            this.courtInfo1 = this.courtInfo2
            this.courtInfo2 = {}
            this.cid1 = this.cid2
            this.cid2 = 0
          } else if (selectIndex === 1) {
            /* courtcourt*/
            scatterData[courtIndex].sdi = 0 // 
            courtControl.splice(selectIndex, 1)
            lngAndLatEnd['coord'] = []

            // second court hidden
            this.courtLength = 1
            // court
            this.courtInfo2 = {}
            this.cid2 = 0
          } else {
            /* court */
            scatterData[courtControl[0]].sdi = 0 // 
            scatterData[courtControl[1]].sdi = 0 // 
            courtControl = [] // court
            lngAndLatEnd['coord'] = [] // End

            courtControl.push(courtIndex) // 
            lngAndLatStart['coord'] = [longitude, latitude] // start

            this.courtLength = 1
            // courtcourt
            this.courtInfo1 = tempCourt
            this.courtInfo2 = {}
            this.cid1 = cid
            this.cid2 = 0
          }
          break
      }
      mapChart.setOption(options)
    },
    courtCloseClick() {
      this.courtLength = 0
      scatterData.forEach(item => item.sdi !== 0 && (item.sdi = 0))
      options.series[1].data = []
      lngAndLatStart['coord'] = []
      lngAndLatEnd['coord'] = []
      courtControl = []
      this.courtInfo1 = {}
      this.courtInfo2 = {}
      this.cid1 = 0
      this.cid2 = 0
      mapChart.setOption(options)
    },
    handleDestory() {
      //  destroy 
      this.$destroy()
    },
