</script>

<style lang="scss">
.v-map {
  width: 100%;
  background-color: #1a253a;
  cursor: pointer;
  position: relative;
  .map__tip {
    position: absolute;
    left: 0;
    top: 0;
    width: 360px;
    height: 0;
    border-style: solid;
    border-width: 0 40px 40px 0px;
    border-color: transparent transparent rgba(49, 60, 94, 0.8) transparent;
    transform: rotateX(180deg);
    .content {
      width: 320px;
      height: 40px;
      line-height: 40px;
      color: $white;
      margin-left: 20px;
      transform: rotateX(180deg);
    }
  }
  .court-dialog {
    width: 260px;
    height: 460px;
    background-color: #ffffff;
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    padding: 15px;
    box-sizing: border-box;
    display: flex;
  }
  .court-pointer {
    width: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 6px;
    .circle {
      width: 14px;
      height: 14px;
      line-height: 10px;
      text-align: center;
      border-radius: 50%;
      background-color: $primary-color;
      &::before {
        content: '';
        display: inline-block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $br-color-base;
      }
    }
    .dashed {
      width: 0px;
      height: 150px;
      border-right: 1px dashed $primary-color;
      margin: 3px 0;
    }
  }
  .court-wrapper {
    .court {
      img {
        width: 180px;
        height: 100px;
      }
      h3 {
        font-size: $fs-16;
        padding: 3px 0;
        color: $text-color;
      }
      h4 {
        color: $text-color-scd;
      }
    }
    .duration {
      width: 180px;
      height: 40px;
      line-height: 40px;
      border-radius: 5px;
      background-color: #f7f7f7;
      padding: 0 15px;
      box-sizing: border-box;
      margin: 56px 0;
      .time {
        float: right;
        font-weight: 600;
        color: $text-color;
      }
    }
  }
  .court-close {
    position: absolute;
    right: 0;
    top: 0;
    transition: transform 0.3s linear;
    .icon-guanbi {
      font-size: $fs-20;
      color: $error-color;
    }
    &:hover {
      transform: rotate(360deg);
    }
  }
}
</style>
