/**
 * 
 * 0-99
 * @param {number} num 
 * return {string} 
 */
export function numToChinese(num) {
  num = Number(num)
  var upperCaseNumber = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  var length = String(num).length
  if (length == 1) {
    return upperCaseNumber[num]
  } else if (length == 2) {
    if (num == 10) {
      return upperCaseNumber[num]
    } else if (num > 10 && num < 20) {
      return '' + upperCaseNumber[String(num).charAt(1)]
    } else {
      return upperCaseNumber[String(num).charAt(0)] + '' + upperCaseNumber[String(num).charAt(1)].replace('', '')
    }
  }
}
