import service from '@/utils/service'

/* 
 * @param  {Object} data 
 * @return {Object|Promise} Promise
 */
export function getNewsList(data) {
  return service.request({
    url: `/api/EuropeInput/GetAdvisoryList`,
    method: 'POST',
    data,
  })
}

